.align {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

/* helpers/grid.css */
.grid {
  margin-left: auto;
  margin-right: auto;
  max-width: 20rem;
  width: 90%;
}

/* helpers/hidden.css */
.hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

/* helpers/icon.css */
.icons {
  display: none;
}
.icon {
  display: inline-block;
  fill: #606468;
  font-size: 1rem;
  height: 1em;
  vertical-align: middle;
  width: 1em;
}
* {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 100%;
  height: 100%;
}
body {
  background-color: #2c3338;
  color: #606468;
  font-family: 'Open Sans', sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  height: 100%;
  line-height: 1.5;
  margin: 0;
  min-height: 100vh;
}

/* modules/anchor.css */
a {
  color: #eee;
  outline: 0;
  text-decoration: none;
}
a:focus,
a:hover {
  text-decoration: underline;
}
input {
  background-image: none;
  border: 0;
  color: inherit;
  font: inherit;
  margin: 0;
  outline: 0;
  padding: 0;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
}
.submit {
  cursor: pointer;
}
.form {
  margin: calc(0.875rem * -1);
}
.form input[type='password'],
.form input[type='text'],
.form .submit {
  width: 100%;
}
.form__field .disableBtn{
  cursor: default;
  background-color:#fff !important;
  color:#9e9e9e !important;
  opacity: 0.8;
}
.form__field .show{
  opacity: 1 !important;
}
.form__field .hide2{
  display: none;
}
.form__field .bgColorRed{
  color: red !important;
}
.form__field {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin:  0.875rem;
}
.form__field select{
  width: 100%;
  margin: 0rem 0rem;
  border-radius: 5px;
  background-color: #2c3338;
  color:white;
  background-color: #3b4148;
  font-size:16px;
  border:0;
}
.form__field select::focus{
  outline: -webkit-focus-ring-color auto 0px;
}
.form__field select option{
  background-color:#2c3338;
  color:white;
  font-size:16px;
}
.form__input {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.login {
  color: #eee;
}
.login label,
.login input[type='text'],
.login input[type='password'],
.login .submit {
  border-radius: 0.25rem;
  padding: 1rem;
  border: 0;
  height:51px;
}
.login label {
  background-color: #363b41;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}
.login input[type='password'],
.login input[type='text'] {
  background-color: #3b4148;
}
.login input[type='password']:focus,
.login input[type='password']:hover,
.login input[type='text']:focus,
.login input[type='text']:hover {
  background-color: #434a52;
}
.login .submit {
  background-color: #50AC58;
  color: #eee;
  font-weight: 700;
  text-transform: uppercase;
}
.login .submit:focus,
.login .submit:hover {
  background-color: #479a4e;
}

/* modules/text.css */
p {
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
}
.text--center {
  text-align: center;
}
ul.evnSelect{
  list-style: none;
  margin: 0;
  padding: 0;
	overflow: auto;
}
ul.evnSelect li{
  color: #AAAAAA;
  display: inline-block;
  position: relative;
  float: left;
  width: 139px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  border-bottom: 1px solid #333;
  padding-left:14px;
}
ul.evnSelect li input[type=radio]{
  position: absolute;
  visibility: hidden;
}
ul.evnSelect li label{
  display: block;
  position: relative;
  font-weight: 300;
  font-size: 1.35em;
  margin: 10px auto;
  height: 30px;
  z-index: 9;
  cursor: pointer;
  -webkit-transition: all 0.25s linear;
     padding: 5px;
}
ul.evnSelect li:hover label{
	color: #FFFFFF;
}
ul.evnSelect li .check{
  display: block;
  position: absolute;
  border: 5px solid #AAAAAA;
  border-radius: 100%;
  height: 16px;
  width: 16px;
  top: 16px;
  left: 13px;
  z-index: 5;
  transition: border .25s linear;
  -webkit-transition: border .25s linear;
  z-index: 100;
}
ul.evnSelect li:hover .check {
  border: 5px solid #FFFFFF;
}
ul.evnSelect li .check::before {
  display: block;
  position: absolute;
	content: '';
  border-radius: 100%;
  height: 8px;
  width: 8px;
  top: 4px;
	left: 4px;
  margin: auto;
	transition: background 0.25s linear;
	-webkit-transition: background 0.25s linear;
}
input[type=radio]:checked ~ .check {
  border: 5px solid #8aab14;
}
input[type=radio]:checked ~ .check::before{
  background-color: #8aab14;
}
input[type=radio]:checked ~ label{
  color: white;
}
.whiteText{
  color: white;
}
.whiteText h1{
  margin-bottom: 0;
}
.whiteText h3{
  margin-top: 3px;
  margin-bottom: 0;
}
.vue-image-slider{
  background: #ddd;
  width: 100%!important;
  height: 100%!important;
  position: fixed !important;
  top: 0;
  left: 0;
}
.vue-image-slider > div,
.vue-image-slider > div > div{
  width: 100%!important;
  height: 100%!important;
}
.vue-image-slider > div > div img{
  -o-object-fit: contain;
     object-fit: contain;
  width: 100%;
  height: 100%;
}
.vue-image-slider-close{
  position: fixed;
  top: 50px;
  right: 50px;
  background: #a0a0a0;
  color: white;
  padding: 7px 15px;
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
}



