
.container {
     position: relative;
}
.fade-enter-active, .fade-leave-active {
     -webkit-transition: all 0.9s ease;
     transition: all 0.9s ease;
     overflow: hidden;
     visibility: visible;
     position: absolute;
     width:100%;
     opacity: 1;
}
.fade-enter, .fade-leave-to {
     visibility: hidden;
     width:100%;
     opacity: 0;
}
img {
     width:100%;
     height:100%
}
.prev, .next {
     cursor: pointer;
     position: absolute;
     top: 40%;
     width: auto;
     padding: 16px;
     color: white;
     font-weight: bold;
     font-size: 18px;
     -webkit-transition: 0.7s ease;
     transition: 0.7s ease;
     border-radius: 0 4px 4px 0;
     text-decoration: none;
     -webkit-user-select: none;
        -moz-user-select: none;
         -ms-user-select: none;
             user-select: none;
}
.next {
     right: 0;
}
.prev {
     left: 0;
}
.prev:hover, .next:hover {
     background-color: rgba(0,0,0,0.9);
}



